.popup {
  color: #4A4A4A;
  position: fixed;
  width: 100%;
  height: 100%;
  z-index: 1000;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transition: opacity $trs2, visibility $trs2;
  &-block {
    margin: auto;
    padding: 20px;
    width: 100%;
    max-width: 700px;
    &__container {
      padding: 50px 40px;
      background-color: #fff;
      min-height: 400px;
      transform: translateY(20px);
      transition: transform $trs2;
    }
  }
  &.active {
    opacity: 1;
    visibility: visible;
    pointer-events: all;
    .popup-block__container {
      transform: translateY(0);
    }
  }
  &__close {
    position: absolute;
    top: 10px;
    right: 10px;
    width: 50px;
    height: 50px;
    z-index: 2;
    display: block;
    &::before, 
    &::after {
      content: '';
      display: block;
      position: absolute;
      top: 50%;
      left: 50%;
      width: 30px;
      height: 2px;
      background-color: #000;
      transition: background-color $trs1;
    }
    &::before {
      transform: translate(-50%, -50%) rotate(45deg);
    }
    &::after {
      transform: translate(-50%, -50%) rotate(-45deg);
    }
    &:hover {
      &::before, 
      &::after {
        background-color: $color-accent;
      } 
    }
  }
  &__title {
    display: block;
    width: 100%;
    font-size: 36px;
    margin: 0 0 30px 0;
    text-align: center;
  }
  @media (max-width:991px) {
    &__title {
      font-size: 32px;
    }
  }
  @media (max-width:575px) {
    &-block {
      &__container {
        padding: 50px 20px;
      }
    }
    &__close {
      top: 5px;
      right: 5px;
    }
    &__title {
      font-size: 24px;
    }
  }
}

.popup-alert {
  font-size: 18px;
  &__container {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
  }
  &__icon {
    width: 80px;
    height: 80px;
    margin-bottom: 30px;
  }
  &__title {
    font-size: 36px;
    margin: 0 0 15px 0;
  }
  p {
    margin: 0;
  }
  &__scene {
    position: relative;
    margin: 25px 40px 0 40px;
    &-image {
      width: 100%;
      height: auto;
      display: block;
      position: relative;
      z-index: 1;
    }
    &-cursor {
      width: 25px;
      height: 40px;
      position: absolute;
      z-index: 3;
      top: 50%;
      left: 8.5%;
      animation: move 2s ease-in-out infinite;
    }
    &-check {
      position: absolute;
      width: 6%;
      height: auto;
      transform: translate(-50%, -50%);
      top: 49%;
      left: 8.6%;
      z-index: 2;
      animation: click 2s ease-in-out infinite;
    }
    @keyframes move {
      0% {
        transform: translate(100px, 20px);
        opacity: 0;
      }
      10% {
        opacity: 1;
      }
      70% {
        transform: translate(0);
      }
      80% {
        opacity: 1;
      }
      100% {
        opacity: 0;
      }
    }
    @keyframes click {
      0% {
        opacity: 1;
      }
      10% {
        opacity: 0;
      }
      70% {
        opacity: 0;
      }
      100% {
        opacity: 1;
      }
    }
  }
  &__button {
    margin: 40px 0 0 0;
    width: 180px;
  }
  @media (max-width:575px) {
    font-size: 16px;
    &__icon {
      width: 50px;
      height: 50px;
      margin-bottom: 20px;
    }
    &__title {
      font-size: 24px;
    }
    &__scene {
      margin: 15px -20px 0 -20px;
    }
    &__button {
      margin: 30px 0 0 0;
    }
  }
}