.errorlist {
  color: red;
}
input.error,  .active-label + .error {
  border-color: red !important;
}
.system-messages {
  margin: 0 auto;
  max-width: 400px;
}

.header-link {
  &:not(:first-child) {
    margin-left: 10px;
  }
}

.select-small {
  min-width: 100px;
  height: 36px;
  .control-label {
    line-height: 36px !important;
    top: 0 !important;
    left: 0 !important;
    padding-left: 15px;
    font-size: 14px !important;
    text-indent: 0 !important;
    transition: opacity $trs1, visibility $trs1;
    &.active-label {
      opacity: 0 !important;
      visibility: hidden !important;
    }
  }
  .select2-selection__rendered {
    line-height: 32px !important;
    padding: 0 13px !important;
  }
  .select2-selection--single {
    padding: 0 !important;
    min-height: 36px !important;
    height: 36px !important;
    &:after {
      border-width: 8px 6px 0;
      top: 13px;
      right: 15px;
    }
  }
}

.header {
  &__phone {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    margin-right: 50px;
    a {
      margin: 0;
    }
  }
  &__call-btn {
    font-weight: normal;
  }
  @media (min-width: 851px) {
    &__call-btn {
      color: $color-accent !important;
      border-bottom: 1px solid $color-accent !important;
      &:hover {
        color: $color-accent-hover !important;
        border-color: transparent !important;
      }
    }
  }
  @media (max-width: 850px) {
    &__contacts {
      a {
        color: #fff !important;
        transition: opacity $trs1;
        &:hover {
          opacity: 0.8;
        }
      }
    }
    &__phone {
      margin-bottom: 20px;
      align-items: flex-start;
    }
    &__call-btn {
      transition: opacity $trs1, border-color $trs1;
      border-bottom: 1px solid #fff;
      &:hover {
        border-color: transparent #fff;
      }
    }
  }
}

.n-form {
  &__container {
    margin-bottom: -20px;
  }
  &__item {
    margin-bottom: 20px;
  }
  &__button {
    width: 100%;
  }
}